import React from 'react';

import './StarRating.scss';

interface Props {
  size: 'sm' | 'md' | 'lg';
  rating: number;
  setRating: (star: number) => void;
}

const mappingSize = { sm: 14, md: 20, lg: 30 };

const StarRating: React.FC<Props> = (props) => {
  const { size, rating, setRating } = props;

  const stars = [];
  for (let i = 1; i <= rating; i++) stars.push('hb-icon-star-s star-default checked');
  for (let i = rating + 1; i <= 5; i++) stars.push('hb-icon-star-s star-default');

  return (
    <div className="d-flex justify-content-evenly" style={{ width: '90%' }}>
      {stars.map((item: string, index: number) => (
        <span
          key={index}
          className={item}
          style={{ fontSize: mappingSize[size] }}
          onClick={() => setRating(index + 1)}
        />
      ))}
    </div>
  );
};

export default StarRating;
