import { CCol, CRow, CTextarea } from '@coreui/react';
import { defaultAvatar } from 'extensions';
import { HBButtonFull, HBButtonSmall, HBModalConfirm } from 'hbBaseClass';
import React from 'react';
import { RouteComponentProps } from 'react-router';
import { useHistory } from 'react-router-dom';
import StarRating from './StarRating';

import './Feedback.scss';
import { useState } from 'react';
import { KMSDiv } from 'components';

const Feedback: React.FC<RouteComponentProps> = (props) => {
  const {} = props;

  const history = useHistory();
  const [feedbackInput, setFeedbackInput] = useState('');
  const [rating, setRating] = useState(0);
  const [show, setShow] = useState(false);

  /**
   *
   * handle close popup confirm
   * @returns
   */
  const handleClose = () => setShow(false);

  return (
    <>
      <div className="petrol">
        <HBModalConfirm
          show={show}
          handleClose={handleClose}
          title="Bewertung gespeichert"
          content="Vielen Dank für Ihre Bewertung"
          up="Zurück"
          upCallback={() => history.push('/consulting')}
        />
      </div>
      <div className="wide">
        <div className="Feedback-wrapper hb-mx-20">
          <div className="hb-my-28">
            <HBButtonSmall color="nightblue" onClick={() => history.push('/consulting')}>
              <i className="hb-icon-arrow-left hb-icon-md" /> Zurück
            </HBButtonSmall>
          </div>
          <h3 className="Feedback-title">Wie gut war ihr Beratungsgespräch?</h3>
          <div className="Feedback-container">
            <CRow>
              <CCol md={4}>
                <div className="Feedback-leftContent__wrapper">
                  <KMSDiv backgroundImage={defaultAvatar} className="Feedback-leftContent__nurseAvatar" />
                  <div className="Feedback-leftContent__nurseName">Maximilian Mustermann</div>
                </div>
              </CCol>
              <CCol md={8}>
                <div className="Feedback-rightContent__wrapper">
                  <p className="Feedback-rightContent__intro">
                    Bewerten Sie in Sternen: 5 Sterne für hervorragend und 1 Stern für nicht zufriedenstellend?
                  </p>

                  <div className="Feedback-rightContent__rate">
                    {/* 
                  Componet parent need value star, but logic handle star rating need handle by component child
                  */}
                    <StarRating size="lg" rating={rating} setRating={setRating} />
                  </div>

                  <h3 className="Feedback-rightContent__title">Ihr Kommentar</h3>

                  <p className="Feedback-rightContent__subtitle">
                    Erklären Sie, was Ihnen an der Beratung gut oder nicht gefallen hat.
                  </p>

                  <p className="Feedback-rightContent__content">
                    Wichtig: Bitte geben Sie konstruktive Kritik und achten Sie auf eine höfliche Formulierung.
                  </p>

                  <CTextarea
                    type="text"
                    maxLength={500}
                    value={feedbackInput}
                    onChange={(e: any) => setFeedbackInput(e.target.value)}
                    name="feedback"
                    rows={10}
                    className="Feedback-rightContent__input"
                    style={{ resize: 'none' }}
                  />

                  <div className="Feedback-rightContent__btnSubmit">
                    <HBButtonFull onClick={() => setShow(true)}>Ihre Bewertung senden</HBButtonFull>
                  </div>
                </div>
              </CCol>
            </CRow>
          </div>
        </div>
      </div>
    </>
  );
};
export default Feedback;
